import React from "react";
import { css } from "@emotion/css";
import styled from "@emotion/styled/macro";
import { ReactComponent as HomeIcon } from "../svgs/light/home.svg";
import { ReactComponent as SearchIcon } from "../svgs/light/search.svg";
import { ReactComponent as BooksIcon } from "../svgs/light/books.svg";
import { NavLink } from "react-router-dom";

const Nav = styled.nav`
  background-color: rgba(0, 0, 0, 0.75);
  @media screen and (min-width: 768px) {
    background-color: #000;
    margin-bottom: 32px;
  }
`;

const List = styled.ul`
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: space-around;
  @media screen and (min-width: 768px) {
    display: inline;
  }
`;

const ListItem = styled.li`
  
`;

const listStyle = css`
  font-size: 11px;
  line-height: 16px;
  font-weight: bold;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 10px 16px;
  border-radius: 4px;
  cursor: pointer;
  color: #b2b2b2;
  fill: #b2b2b2;
  text-decoration: none;
  &:hover {
    color: #fff;
    fill: #fff;
  }
  @media screen and (min-width: 768px) {
    flex-direction: row;
    font-size: 14px;
  }
`;

const listStyleActive = css`
  color: #fff;
  fill: #fff;
  @media screen and (min-width: 768px) {
    background-color: #282828;
  }
`;

const svgStyle = css`
  fill: inherit;
  height: 20px;
  width: 20px;
  @media screen and (min-width: 768px) {
    margin-right: 16px;
  }
`;

function Navigation() {
  return (
    <Nav>
      <List>
        <ListItem>
          <NavLink
            to="/artify"
            exact
            className={listStyle}
            activeClassName={listStyleActive}
          >
            <HomeIcon className={svgStyle} />
            Home
          </NavLink>
        </ListItem>
        <ListItem>
          <NavLink
            to="/search"
            exact
            className={listStyle}
            activeClassName={listStyleActive}
          >
            <SearchIcon className={svgStyle} />
            Search
          </NavLink>
        </ListItem>
        <ListItem>
          <NavLink
            to="/library"
            exact
            className={listStyle}
            activeClassName={listStyleActive}
          >
            <BooksIcon className={svgStyle} />
            Your library
          </NavLink>
        </ListItem>
      </List>
    </Nav>
  );
}

export default Navigation;
