import { css } from "@emotion/css";
import styled from "@emotion/styled/macro";
import React from "react";
import { Link } from "react-router-dom";
import { ICollection } from "./models";
import { formatDuration } from "./utils";

const Header = styled.header`
  min-height: 200px;
  display: flex;
  height: 100%;
  @media screen and (min-width: 768px) {
    min-height: 340px;
    flex: 0 1;
  }
`;

interface IFigure {
  baseColor: string;
}

const Figure = styled.figure<IFigure>`
  width: 100%;
  height: 100%;
  background: linear-gradient(
    180deg,
    ${(props) => props.baseColor} 0%,
    #000 95%
  );
  padding: 40px 24px 8px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  @media screen and (min-width: 768px) {
    padding: 48px 32px 24px 32px;
    flex-wrap: nowrap;
    background: linear-gradient(
      180deg,
      ${(props) => props.baseColor} 0%,
      #121212 95%
    );
  }
`;

const Image = styled.img`
  box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
  height: 100%;
  margin: 0 auto;
  max-height: 200px;
  @media screen and (min-width: 768px) {
    margin: 0;
  }
`;

const Caption = styled.figcaption`
  flex: 0 0 100%;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 768px) {
    flex: 0 1 auto;
    height: 100%;
    max-height: 200px;
    padding-left: 24px;
    padding-top: 32px;
    justify-content: space-between;
  }
`;

const CollectionType = styled.span`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    font-size: 14px;
  }
`;

const H1 = styled.h1`
  font-size: 28px;
  color: #fff;
  margin: 8px 0;
  @media screen and (min-width: 768px) {
    font-size: 48px;
  }
`;

const linkStyle = css`
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  @media screen and (min-width: 768px) {
    display: none;
  }
`;

const ArtistInfo = styled.span`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline;
    color: #b2b2b2;
    font-size: 14px;
    & .${linkStyle} {
      display: inline;
    }
  }
`;

const ArtistSubInfo = styled.span`
  color: #b2b2b2;
  font-size: 14px;
  @media screen and (min-width: 768px) {
    display: none;
  }
`;

const Description = styled.p`
  margin: 0 0 4px;
  font-size: 14px;
  color: #b2b2b2;
`;

interface ICollectionHeaderProps extends ICollection {
  duration: number;
}

function CollectionHeader(
  props: React.PropsWithoutRef<ICollectionHeaderProps>
) {
  return (
    <Header>
      <Figure baseColor={props.baseColor}>
        <Image src={`./media/${props.mediaId}`} alt="" />
        <Caption>
          <CollectionType>{props.type}</CollectionType>
          <H1>{props.title}</H1>
          {props.description && <Description>{props.description}</Description>}
          <Link to="/artify" className={linkStyle}>
            Arthur A. Ferreira
          </Link>
          <ArtistInfo>
            <Link to="/artify" className={linkStyle}>
              Arthur A. Ferreira
            </Link>
            {` • ${props.releaseYear} • ${
              props.tracks.length
            } Songs • ${formatDuration(props.duration)}`}
          </ArtistInfo>
          <ArtistSubInfo>
            {`${props.type} • ${props.releaseYear}`}
          </ArtistSubInfo>
        </Caption>
      </Figure>
    </Header>
  );
}

export default CollectionHeader;
