import React from "react";
import styled from "@emotion/styled/macro";
import Navigation from "./Navigation";
import Logo from "./Logo";
import SavedPlaylist from "./SavedPlaylists";
import { ICollection } from "./models";

const Container = styled.div`
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 56px;
  z-index: 2;
  @media screen and (min-width: 768px) {
    position: relative;
    background-color: #000;
    width: 240px;
    padding: 24px 8px;
    display: flex;
    flex-direction: column;
    height: calc(100% - 90px);
  }
`;

const Divider = styled.hr`
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
    border-color: #282828;
    border-top-width: 0;
    width: calc(100% - 20px);
  }
`;

interface ISidebarProps {
  collections: ICollection[];
}

function Sidebar(props: React.PropsWithoutRef<ISidebarProps>) {
  return (
    <Container>
      <Logo></Logo>
      <Navigation></Navigation>
      <Divider></Divider>
      <SavedPlaylist items={props.collections}></SavedPlaylist>
    </Container>
  );
}

export default Sidebar;
