import React from "react";
import styled from "@emotion/styled/macro";
import { ICollection } from "./models";
import { NavLink } from "react-router-dom";
import { css } from "@emotion/css";

const PlaylistContainer = styled.ul`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline-block;
    list-style: none;
    flex: 1 1 100%;
    margin: 0;
    padding: 0 12px;
  }
`;

interface IPlaylistProps {
  active?: boolean;
}

const Playlist = styled.li<IPlaylistProps>`
  cursor: default;
  
  margin-top: 8px;
`;

const linkStyle = css`
  color: #b2b2b2;
  text-decoration: none;
  font-size: 14px;
  font-weight: 600;
  &:hover {
    color: #fff;
  }
`;

const linkActive = css`
  color: #fff;
`;

interface ISavedPlaylistProps {
  items: ICollection[];
}

function SavedPlaylist(props: React.PropsWithoutRef<ISavedPlaylistProps>) {
  return (
    <PlaylistContainer>
      {props.items.map((col) => (
        <Playlist key={col.id}>
          <NavLink
            to={`/artify/${col.id}`}
            className={linkStyle}
            activeClassName={linkActive}
          >
            {col.title}
          </NavLink>
        </Playlist>
      ))}
    </PlaylistContainer>
  );
}

export default SavedPlaylist;
