import React from "react";
import styled from "@emotion/styled/macro";
import { css } from "@emotion/css";
import { ReactComponent as ListIcon } from "../svgs/light/list.svg";
import { ReactComponent as SpeakersIcon } from "../svgs/light/speakers.svg";
import { ReactComponent as VolumeIcon } from "../svgs/light/volume.svg";
import { ReactComponent as ExpandIcon } from "../svgs/light/expand-alt.svg";

const IconButton = styled.button`
  height: 32px;
  width: 32px;
  fill: #b2b2b2;
  background-color: transparent;
  border: none;
  margin: 0 4px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    fill: #fff;
  }
  &:disabled {
    fill: #535353;
  }
`;

const Slider = styled.input`
  overflow: hidden;
  width: 120px;
  -webkit-appearance: none;
  background-color: #535353;
  height: 4px;
  border-radius: 2px;
  outline: none;

  ::-moz-range-thumb {
    width: 10px;
    appearance: none;
    height: 10px;
    background: #fff;
    box-shadow: -80px 0 0 80px #b3b3b3;
  }
  ::-webkit-slider-thumb {
    width: 10px;
    appearance: none;
    height: 10px;
    background: #fff;
    box-shadow: -80px 0 0 80px #b3b3b3;
  }

  :hover::-moz-range-thumb {
    box-shadow: -80px 0 0 80px #1db954;
  }
  :hover::-webkit-slider-thumb {
    box-shadow: -80px 0 0 80px #1db954;
  }
`;

const svgSmall = css`
  height: 16px;
  width: 16px;
`;

const Container = styled.div`
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
  }
`;

interface IAdditionalControlsProps {
  volume: number;
  onVolumeChange: (volume: number) => void;
}

function AdditionalControls({
  volume,
  onVolumeChange,
}: React.PropsWithoutRef<IAdditionalControlsProps>) {
  const onChangeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    onVolumeChange(+e.target.value);
  };

  return (
    <Container>
      <IconButton>
        <ListIcon className={svgSmall} />
      </IconButton>
      <IconButton>
        <SpeakersIcon className={svgSmall} />
      </IconButton>
      <IconButton>
        <VolumeIcon className={svgSmall} />
      </IconButton>
      <Slider
        type="range"
        onChange={onChangeHandler}
        min="0"
        max="1"
        value={volume}
        step="0.01"
      ></Slider>
      <IconButton>
        <ExpandIcon className={svgSmall} />
      </IconButton>
    </Container>
  );
}

export default AdditionalControls;
