import React from "react";
import styled from "@emotion/styled/macro";
import { css } from "@emotion/css";
import { ReactComponent as SpotifyLogo } from "../svgs/brands/spotify.svg";

const logoStyle = css`
  height: 40px;
  width: 40px;
  fill: #fff;
  margin-right: 4px;
`;

const LogoContainer = styled.div`
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
    color: #fff;
    font-size: 24px;
    line-height: 28px;
    align-items: center;
    font-weight: bold;
    padding: 0 16px;
    margin: 0 0 24px;
  }
`;

const Sup = styled.sup`
  font-size: 8px;
`;

function Logo() {
  return (
    <LogoContainer>
      <SpotifyLogo className={logoStyle}></SpotifyLogo>
      Artify <Sup>©</Sup>
    </LogoContainer>
  );
}

export default Logo;
