import React from "react";
import styled from "@emotion/styled/macro";
import { css } from "@emotion/css";
import { ReactComponent as HeartIcon } from "../svgs/light/heart.svg";
import { ReactComponent as PausedIcon } from "../svgs/solid/pause.svg";
import { ReactComponent as PlayIcon } from "../svgs/solid/play.svg";
import { ITrack } from "./models";
import { Link } from "react-router-dom";

const Figure = styled.figure`
  margin: 0;
  display: flex;
  padding: 4px 4px 0;
  width: 100%;
  height: 36px;
  @media screen and (min-width: 768px) {
    width: 240px;
    padding: 16px;
    height: auto;
  }
`;

const Caption = styled.figcaption`
  padding-left: 4px;
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  @media screen and (min-width: 768px) {
    padding-left: 16px;
    flex: 0 1 auto;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
  }
`;

const Image = styled.img`
  border-radius: 3px;
  height: 100%;
  box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
  @media screen and (min-width: 768px) {
    border-radius: 0px;
  }
`;

const svgStyle = css`
  fill: #b2b2b2;
  height: 20px;
  width: 20px;
  margin: 0 8px;
  &:hover {
    fill: #fff;
  }
  @media screen and (min-width: 768px) {
    height: 14px;
    width: 14px;
    margin: 0 0 0 24px;
  }
`;

const trackStyle = css`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline;
    color: #fff;
    text-decoration: none;
    font-size: 14px;
    line-height: 16px;
    &:hover {
      text-decoration: underline;
    }
  }
`;

const artistStyle = css`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline;
    color: #b2b2b2;
    text-decoration: none;
    font-size: 11px;
    line-height: 16px;
    &:hover {
      color: #fff;
      text-decoration: underline;
    }
  }
`;

const Cite = styled.cite`
  font-style: normal;
  text-overflow: ellipsis;
  max-width: 140px;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
`;

const trackArtistStyle = css`
  color: #fff;
  text-decoration: none;
  font-size: 11px;
  line-height: 16px;
  font-weight: bold;
  flex: 1 1 100%;
  max-width: calc(100% - 130px);
  overflow: hidden;
  white-space: nowrap;
  @media screen and (min-width: 768px) {
    display: none;
  }
`;

const hideSmall = css`
  @media screen and (min-width: 768px) {
    display: none;
  }
`;

const ActionContainer = styled.div`
  display: flex;
  align-items: center;
  @media screen and (min-width: 768px) {
    flex: 1 1 100%;
  }
`;

interface IPlayingProps {
  track?: ITrack;
  isPlaying: boolean;
  onPlayPause: (playing: boolean) => void;
}

function Playing({
  track,
  isPlaying = false,
  onPlayPause,
}: React.PropsWithoutRef<IPlayingProps>) {
  return (
    <Figure>
      {track && (
        <>
          <Image src={`/media/${track.mediaId}`} alt="Cover Art" />
          <Caption>
            <Link
              to={`/artify/${track.collectionId}`}
              className={trackArtistStyle}
            >
              {`${track.title} • Arthur Ferreira`}{" "}
            </Link>
            <Link to={`/artify/${track.collectionId}`} className={trackStyle}>
              <Cite>{track.title}</Cite>
            </Link>
            <Link to="/artify" className={artistStyle}>
              Arthur Ferreira
            </Link>

            <ActionContainer>
              <HeartIcon className={svgStyle} />
              {isPlaying ? (
                <PausedIcon
                  onClick={() => onPlayPause(false)}
                  className={`${svgStyle} ${hideSmall}`}
                />
              ) : (
                <PlayIcon
                  onClick={() => onPlayPause(true)}
                  className={`${svgStyle} ${hideSmall}`}
                />
              )}
            </ActionContainer>
          </Caption>
        </>
      )}
    </Figure>
  );
}

export default Playing;
