function reducer(state: ISpotifyState, action: IAction) {
  switch (action.type) {
    case "PLAY_PAUSE":
      return {
        ...state,
        playing: action.playing,
      };
    case "VOLUME_CHANGE":
      return {
        ...state,
        volume: action.volume,
      };
    case "NEXT_SONG": {
      const currentTrack =
        state.queue[state.queue.indexOf(state.currentTrack) + 1];
      if (!currentTrack) {
        return {
          ...state,
          playing: false,
          currentTrack: state.queue[0],
        };
      } else {
        return {
          ...state,
          currentTrack,
        };
      }
    }
    case "PREV_SONG": {
      const currentTrack =
        state.queue[state.queue.indexOf(state.currentTrack) - 1];
      return { ...state, currentTrack };
    }
    case "PLAY_QUEUE": {
      const [currentTrack] = action.queue;
      return {
        ...state,
        playing: true,
        currentTrack: currentTrack,
        queue: action.queue,
        currentCollection: action.currentCollection,
      };
    }
    default:
      return state;
  }
}

export function useAppState(): [ISpotifyState, React.Dispatch<IAction>] {
  const [state, dispatch] = useReducer(reducer, initialState);
  return [state, dispatch];
}

export const DispatchContext = React.createContext<
  ISpotifyState | React.Dispatch<IAction>
>(initialState);
