import React from "react";
import styled from "@emotion/styled/macro";

interface ISectionCardProps {
  title: string;
}

const Section = styled.section`
  padding: 16px 0 16px 24px;
  display: flex;
  flex-wrap: wrap;
  @media screen and (min-width: 768px) {
    padding: 16px 0 16px 32px;
  }
`;

const H2 = styled.h2`
  font-size: 20px;
  margin: 0px;
  font-weight: 700;
  margin-bottom: 16px;
  color: #fff;
  flex: 0 0 100%;
  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
`;

function SectionCard(props: React.PropsWithChildren<ISectionCardProps>) {
  return (
    <Section>
      <H2>{props.title}</H2>
      {props.children}
    </Section>
  );
}

export default SectionCard;
