import React from "react";
import styled from "@emotion/styled/macro";
import { css } from "@emotion/css";
import { ReactComponent as BadgeIcon } from "../svgs/duotone/badge-check.svg";

const Header = styled.header`
  min-height: 220px;
  position: relative;
  ::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    max-width: 100%;
    background-image: url("./media/profile.jpg");
    background-attachment: fixed;
    background-position: right -130px;
    background-repeat: no-repeat;
    background-size: cover;
    filter: grayscale(100%);
    @media screen and (min-width: 768px) {
      background-position: right -240px;
    }
  }
  @media screen and (min-width: 768px) {
    min-height: 382px;
  }
`;

const HeaderContent = styled.div`
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 24px;
  @media screen and (min-width: 768px) {
    padding: 0 32px;
  }
`;

const H1 = styled.section`
  font-size: 40px;
  color: #fff;
  margin: 8px 0 24px;
  text-shadow: 1px 1px #242424a8;
  @media screen and (min-width: 768px) {
    font-size: 72px;
  }
`;

const Subtitle = styled.span`
  color: #fff;
  display: flex;
  align-items: center;
  font-weight: 500;
  text-shadow: 1px 1px #242424a8;
`;

const svgStyle = css`
  height: 20px;
  width: 20px;
  margin-right: 8px;
  .fa-primary {
    fill: #fff;
  }
  .fa-secondary {
    fill: #2e77d0;
    opacity: 1;
  }
`;

function HomeHeader() {
  return (
    <Header>
      <HeaderContent>
        <Subtitle>
          <BadgeIcon className={svgStyle} /> Verified developer
        </Subtitle>
        <H1>Arthur A. Ferreira</H1>
      </HeaderContent>
    </Header>
  );
}

export default HomeHeader;
