import React from "react";
import Spotify from "./spotify/Spotify";
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/artify">
          <Spotify></Spotify>
        </Route>
        <Route exact path="/">
          <Redirect to="/artify" />
        </Route>
        <Route path="*">
          <Redirect to="/" />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
