import React, { useEffect, useState } from "react";
import styled from "@emotion/styled/macro";
import { css } from "@emotion/css";

import { ReactComponent as RandomIcon } from "../svgs/regular/random.svg";
import { ReactComponent as BackwardIcon } from "../svgs/solid/backward.svg";
import { ReactComponent as PausedIcon } from "../svgs/solid/pause.svg";
import { ReactComponent as PlayIcon } from "../svgs/solid/play.svg";
import { ReactComponent as ForwardIcon } from "../svgs/solid/forward.svg";
import { ReactComponent as RepeatIcon } from "../svgs/regular/repeat-alt.svg";
import { ITrack } from "./models";
import { useRef } from "react";
import { formatSeconds } from "./utils";

const IconButton = styled.button`
  display: none;
  @media screen and (min-width: 768px) {
    height: 32px;
    width: 32px;
    fill: #b2b2b2;
    background-color: transparent;
    border: none;
    margin: 0 4px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      fill: #fff;
    }
    &:disabled {
      fill: #535353;
    }
  }
`;

const RoundedIconButton = styled.button`
  display: none;
  @media screen and (min-width: 768px) {
    fill: #000;
    background-color: #fff;
    border: none;
    margin: 0 4px;
    padding: 0;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    :active {
      transform: scale(0.95);
    }
    :hover,
    :focus {
      transform: scale(1.05);
    }
  }
`;

const svgSmall = css`
  height: 16px;
  width: 16px;
`;

const svgPlayIcon = css`
  margin-left: 2px;
`;

const Container = styled.div`
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
`;

const ProgressContainer = styled.div`
  flex: 0 0 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (min-width: 768px) {
    margin-top: 16px;
  }
`;

const Progress = styled.progress`
  flex: 1 1 auto;
  margin: 0 2px;
  appearance: none;
  height: 2px;
  border: none;
  border-radius: 2px;
  background-color: #535353;
  ::-webkit-progress-bar {
    background-color: #535353;
    border-radius: 2px;
  }

  ::-webkit-progress-value {
    background-color: #b3b3b3;
    border-radius: 2px;
  }

  ::-moz-progress-bar {
    background-color: #b3b3b3;
    border-radius: 2px;
  }

  @media screen and (min-width: 768px) {
    height: 4px;
    max-width: 65%;
    margin: 0 8px;
    :hover::-webkit-progress-value {
      background-color: #1db954;
    }
    :hover::-moz-progress-bar {
      background-color: #1db954;
    }
  }
`;

const Span = styled.span`
  display: none;
  @media screen and (min-width: 768px) {
    display: inline;
    font-size: 11px;
    line-height: 16px;
    font-weight: 400;
    color: rgb(179, 179, 179);
  }
`;

interface IControlsProps {
  isPlaying: boolean;
  volume: number;
  track?: ITrack;
  queue: string[];
  currentTrack?: string;
  onPlayPause: (playing: boolean) => void;
  onNext: () => void;
  onPrevious: () => void;
}

function Controls({
  isPlaying,
  volume,
  track,
  queue,
  currentTrack,
  onPrevious,
  onNext,
  onPlayPause,
}: React.PropsWithoutRef<IControlsProps>) {
  const currentVolume = useRef(volume);
  const trackDuration = useRef(1);
  const playing = useRef(isPlaying);
  const onTrackEnd = useRef(onNext);
  const [nextDisabled, setNextDisabled] = useState(true);
  const [previousDisabled, setPreviousDisabled] = useState(true);
  const [timer, setTimer] = useState(0);

  useEffect(() => {
    playing.current = isPlaying;
    if (isPlaying) {
      if (window.speechSynthesis.paused) {
        window.speechSynthesis.resume();
      }
      if (timer > trackDuration.current) {
        onTrackEnd.current();
      } else {
        const timeout = setTimeout(() => {
          setTimer((p) => p + 0.1);
        }, 100);
        return () => {
          clearTimeout(timeout);
        };
      }
    } else {
      window.speechSynthesis.pause();
    }
  }, [timer, isPlaying]);

  useEffect(() => {
    if (track && speechSynthesis) {
      trackDuration.current = track.duration;
      const utterThis = new SpeechSynthesisUtterance(`${track.description}`);
      utterThis.rate = 1.25;
      utterThis.volume = currentVolume.current;
      setTimer(0);

      speechSynthesis.speak(utterThis);
      if (!playing.current) {
        speechSynthesis.pause();
      }
    }
    return () => {
      window.speechSynthesis.cancel();
    };
  }, [track]);

  useEffect(() => {
    currentVolume.current = volume;
  }, [volume]);

  useEffect(() => {
    if (currentTrack) {
      const isFirst = queue.indexOf(currentTrack) === 0;
      const isLast = queue.indexOf(currentTrack) === queue.length - 1;
      setPreviousDisabled(isFirst);
      setNextDisabled(isLast);
    }
  }, [currentTrack, queue]);

  const onPreviousClickHandler = () => {
    onPrevious();
  };

  const onNextClickHandler = () => {
    onNext();
  };

  const onPlayPauseClickHandler = () => {
    onPlayPause(!isPlaying);
  };

  return (
    <Container>
      <IconButton type="button" disabled>
        <RandomIcon className={svgSmall} />
      </IconButton>
      <IconButton
        type="button"
        disabled={previousDisabled}
        onClick={onPreviousClickHandler}
      >
        <BackwardIcon className={svgSmall} />
      </IconButton>
      <RoundedIconButton onClick={onPlayPauseClickHandler}>
        {isPlaying ? (
          <PausedIcon className={svgSmall} />
        ) : (
          <PlayIcon className={`${svgSmall} ${svgPlayIcon}`} />
        )}
      </RoundedIconButton>
      <IconButton
        type="button"
        disabled={nextDisabled}
        onClick={onNextClickHandler}
      >
        <ForwardIcon className={svgSmall} />
      </IconButton>
      <IconButton disabled type="button">
        <RepeatIcon className={svgSmall} />
      </IconButton>
      <ProgressContainer>
        <Span>{formatSeconds(timer)}</Span>
        <Progress max={trackDuration.current} value={timer}></Progress>
        <Span>{formatSeconds(trackDuration.current)}</Span>
      </ProgressContainer>
    </Container>
  );
}

export default Controls;
