import styled from "@emotion/styled/macro";
import React, { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import CollectionHeader from "./CollectionHeader";
import { ICollection, ITrack } from "./models";
import { DispatchContext, IAction } from "./state";
import Track from "./Track";
import TrackList from "./TrackList";
import { ReactComponent as PlayIcon } from "../svgs/solid/play.svg";
import { ReactComponent as HeartIcon } from "../svgs/light/heart.svg";
import { css } from "@emotion/css";

const CollectionContainer = styled.div`
  flex: 1 1;
  background: #000;
  padding: 0 16px;
  @media screen and (min-width: 768px) {
    background: #121212;
  }
`;

const svgStyle = css`
  fill: inherit;
  height: 20px;
  width: 20px;
  @media screen and (min-width: 768px) {
    height: 24px;
    width: 24px;
  }
`;

const RoundedIconButton = styled.button`
  background: #1db954;
  border: none;
  fill: #000;
  border-radius: 50%;
  padding: 0 0 0 3px;
  height: 48px;
  width: 48px;
  @media screen and (min-width: 768px) {
    height: 56px;
    width: 56px;
    fill: #fff;
    margin-right: 32px;
    :hover {
      transform: scale(1.1);
    }
  }
`;

const IconButton = styled.button`
  background: transparent;
  border: none;
  fill: #b2b2b2;
  padding: 0;
  @media screen and (min-width: 768px) {
    order: 1;
  }
  :hover {
    fill: #fff;
  }
`;

const ActionContainer = styled.div`
  padding: 0 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (min-width: 768px) {
    justify-content: flex-start;
    padding: 24px 32px;
  }
`;

const Copyright = styled.span`
  color: #b2b2b2;
  font-size: 12px;
  padding: 0 24px;
  @media screen and (min-width: 768px) {
    padding: 0 32px;
  }
`;

interface ICollectionProps {
  collections: ICollection[];
  tracks: ITrack[];
  currentTrack?: string;
}

interface IParams {
  id?: string;
}

function Collection(props: React.PropsWithoutRef<ICollectionProps>) {
  let { id } = useParams<IParams>();
  const [collection, setCollection] = useState<ICollection>();
  const [tracks, setTracks] = useState<ITrack[]>([]);
  const [duration, setDuration] = useState(0);
  const dispatch = useContext(DispatchContext) as React.Dispatch<IAction>;

  useEffect(() => {
    const col = props.collections.find((col) => col.id === id);
    if (col) {
      setTracks(props.tracks.filter(({ id }) => col.tracks.indexOf(id) !== -1));
      setCollection(col);
    }
  }, [id, props.collections, props.tracks]);

  useEffect(() => {
    setDuration(tracks.reduce((p, c) => (p += c.duration), 0));
  }, [tracks]);

  const onClickHandler = () => {
    dispatch({
      type: "PLAY_QUEUE",
      queue: tracks.map(({ id }) => id),
      currentCollection: collection?.id,
    });
  };

  const onTrackSelectedHandler = (currentTrack: string) => {
    const currentTrackIndex = tracks.findIndex((t) => t.id === currentTrack);

    dispatch({
      type: "PLAY_QUEUE",
      queue: tracks.slice(currentTrackIndex).map(({ id }) => id),
    });
  };

  return (
    <>
      {collection && <CollectionHeader duration={duration} {...collection}></CollectionHeader>}
      <ActionContainer>
        <IconButton type="button">
          <HeartIcon className={svgStyle} />
        </IconButton>
        <RoundedIconButton type="button" onClick={onClickHandler}>
          <PlayIcon className={svgStyle} />
        </RoundedIconButton>
      </ActionContainer>
      <CollectionContainer>
        <TrackList>
          {tracks.map((track) => (
            <Track
              key={track.id}
              onTrackSelected={onTrackSelectedHandler}
              playing={track.id === props.currentTrack}
              {...track}
            ></Track>
          ))}
        </TrackList>
      </CollectionContainer>
      <Copyright>{`© ${collection?.releaseYear} Artify Ltd.`}</Copyright>
    </>
  );
}

export default Collection;
