import React, { useContext, useState } from "react";
import styled from "@emotion/styled/macro";

import TrackList from "./TrackList";
import Track from "./Track";
import { ITrack } from "./models";
import { DispatchContext, IAction } from "./state";

const Section = styled.section`
  padding: 24px;
  @media screen and (min-width: 768px) {
    padding: 32px;
  }
`;

const H2 = styled.section`
  font-size: 20px;
  margin: 0px;
  font-weight: 700;
  margin-bottom: 16px;
  color: #fff;
  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
`;

const Button = styled.button`
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  font-size: 12px;
  font-weight: 700;
  color: #ffffffb3;
  :hover {
    color: #ffffff;
  }
`;

interface IPopularProps {
  tracks: ITrack[];
  currentTrack?: string;
}

function Popular(props: React.PropsWithoutRef<IPopularProps>) {
  const [showMore, setShowMore] = useState(false);
  const dispatch = useContext(DispatchContext) as React.Dispatch<IAction>;

  const onClickHandler = () => {
    setShowMore(!showMore);
  };

  const onTrackSelectedHandler = (currentTrack: string) => {
    const currentTrackIndex = props.tracks.findIndex(
      (t) => t.id === currentTrack
    );

    dispatch({
      type: "PLAY_QUEUE",
      queue: props.tracks.slice(currentTrackIndex).map(({ id }) => id),
    });
  };

  return (
    <Section>
      <H2>Popular</H2>
      <TrackList>
        {props.tracks.slice(0, showMore ? undefined : 5).map((track) => (
          <Track
            playing={track.id === props.currentTrack}
            onTrackSelected={onTrackSelectedHandler}
            key={track.id}
            {...track}
          ></Track>
        ))}
      </TrackList>
      <Button onClick={onClickHandler}>
        {showMore ? "Show less" : "See More"}
      </Button>
    </Section>
  );
}

export default Popular;
