import React, { useContext } from "react";
import styled from "@emotion/styled/macro";
import HomeHeader from "./HomeHeader";
import Popular from "./Popular";
import SectionCard from "./SectionCard";
import Card from "./Card";
import { ICollection, ITrack } from "./models";
import { DispatchContext, IAction } from "./state";

const ArtistSection = styled.section`
  background: linear-gradient(180deg, #242424 0px, #000 200px);
  @media screen and (min-width: 768px) {
    background: linear-gradient(180deg, #242424 0px, #121212 200px);
  }
`;

interface IHomeProps {
  albums: ICollection[];
  singles: ICollection[];
  playlists: ICollection[];
  popularTracks: ITrack[];
  currentTrack?: string;
  currentCollection?: string;
}

function Home(props: React.PropsWithoutRef<IHomeProps>) {
  const dispatch = useContext(DispatchContext) as React.Dispatch<IAction>;

  const onPlayHandler = (currentCollection: string, queue: string[]) => {
    dispatch({
      type: "PLAY_QUEUE",
      queue,
      currentCollection,
    });
  };

  return (
    <>
      <HomeHeader></HomeHeader>
      <ArtistSection>
        <Popular
          tracks={props.popularTracks}
          currentTrack={props.currentTrack}
        ></Popular>
        <SectionCard title="Albums">
          {props.albums.map((album) => (
            <Card
              key={album.id}
              onPlay={onPlayHandler}
              playing={album.id === props.currentCollection}
              label={"Album"}
              {...album}
            ></Card>
          ))}
        </SectionCard>
        <SectionCard title="Singles">
          {props.singles.map((single) => (
            <Card
              key={single.id}
              onPlay={onPlayHandler}
              playing={single.id === props.currentCollection}
              label={"Single"}
              {...single}
            ></Card>
          ))}
        </SectionCard>
        <SectionCard title="Artist Playlists">
          {props.playlists.map((playlist) => (
            <Card
              key={playlist.id}
              onPlay={onPlayHandler}
              playing={playlist.id === props.currentCollection}
              label={"Playlist"}
              {...playlist}
            ></Card>
          ))}
        </SectionCard>
      </ArtistSection>
    </>
  );
}

export default Home;
